import vue from 'vue';
import vuex from 'vuex';
import cards from '../cards1';

vue.use(vuex);

export default new vuex.Store({
  state: {
    cards: cards
  },
  getters: {
    getAllCards(state) {
      let cards = state.cards['card_set']['card_list'];
      return cards
    }
  }
});
