<template>
  <div class="card-wrapper" v-if="filter">
    <div class="card" :class="colorClass" @mouseenter="showHover" @mouseleave="hideHover">
      <div class="card-title">{{title}} --- {{cardType}}</div>
      <div class="card-hover" v-if="active">
        <img :src=(img) alt="">
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Card",
    props: ['title', 'img', 'cardType'],
    data() {
      return {
        active: false
      }
    },
    methods: {
      showHover() {
        this.active = true
      },
      hideHover() {
        this.active = false
      }
    },
    computed: {
      colorClass() {
        let type = this.cardType.toLowerCase().replace(/\s/g, '');
        return type;
      },
      filter() {
        return this.img !== undefined;
      }
    },
  }
</script>

<style scoped lang="scss">
  .card-wrapper {
    width: 25%;
    padding: 0 20px;
  }

  .card {
    width: 100%;
    border-radius: 100px;
    padding: 10px 20px;
    cursor: pointer;
    user-select: none;
    margin-bottom: 20px;
    position: relative;
    color: #ffffff;
    &.hero {
      background-color: #2c3e50;
    }
    &.spell {
      background-color: #c0392b;
    }
    &.passiveability {
      background-color: #1abc9c;
    }
    &.creep {
      background-color: #95a5a6;
    }
    &.ability {
      background-color: #e67e22;
    }
    &.improvement {
      background-color: #8e44ad;
    }
    &.item {
      background-color: #3498db;
    }
  }

  .card-title {
    font-weight: bold;
  }

  .card-hover {
    position: absolute;
    top: 30px;
    left: 0;
    right: 0;
    z-index: 10;
    img {
      width: 100%;
    }
  }
</style>
